<template>
  <div>
    <blog-header></blog-header>
    <hr/>
    
    <div class="ready_area">
      
        <h3>数据库连接池配置</h3>
        
        <span class = 'required' style="color:red">*</span>数据库类型：<select id='dataBaseType' v-model="database.dataBaseType">
		              <option v-for="item in dataBaseList" v-bind:value="item.value" v-bind:key="item.key">{{item.key}}
                  </option>
	              </select>
        <br/><br/>
        <form id = 'baseConfig'>
          <div class="formArea">
            <div class="columnArea fd-w25">
              <span class = 'required'><span>*</span>ip：</span>
              <div>
                <input name='ip' v-model="database.ip" v-validate="'required'" />
                <span class="errorMsg">{{ errors.first('ip') }}</span>
              </div>
            </div>
            <div class="columnArea fd-w25">
              <span class = 'required'><span>*</span>端口：</span>
              <div>
                <input name='port' v-model="database.port" v-validate="'required'"/>
                <span class="errorMsg">{{ errors.first('port') }}</span>
              </div>
            </div>
            <div class="columnArea fd-w25">
              <span class = 'required'><span>*</span>数据库：</span>
              <div>
                <input name='dataBaseName' v-model="database.dataBaseName" v-validate="'required'"/>
                <span class="errorMsg">{{ errors.first('dataBaseName') }}</span>
              </div>
            </div>
            <div class="columnArea fd-w25">
              <span class = 'required'><span>*</span>用户名：</span>
              <div>
                <input name='user' v-model="database.userName" v-validate="'required'"/>
                <span class="errorMsg">{{ errors.first('user') }}</span>
              </div>
            </div>
            <div class="columnArea fd-w25">
              <span class = 'required'><span>*</span>密码：</span>
              <div>
                <input name='password' v-model="database.password" v-validate="'required'"/>
                <span class="errorMsg">{{ errors.first('password') }}</span>
              </div>
            </div>
          </div>
        </form>
    </div>
    <br/><br/><br/><br/><br/><br/><br/><br/>
    <form id = 'aimsConfig'>
    <div class="ready_area">
        <h3>待清洗库表配置</h3>
        <p>数据库模式：<input v-model = "database.sourceSechma" placeholder="若无，可不填写"/>
        表名：<input v-model = "database.sourceTable" placeholder="若不填写，则清洗整个数据库"/>
        字段名：<input v-model = "database.sourceColumn" /></p>
    </div>
    <br/><br/><br/><br/>
    <div class="ready_area">
        <h3>临时表配置</h3>
        <p>数据库模式：<input v-model = 'database.purposeSechma' placeholder="若无，可不填写"/>
        表名：<input v-model = 'database.tempTable'/>
        源值字段：<input v-model = 'database.oldColumn'/>
        目标值字段：<input v-model = 'database.newColumn'/></p>
    </div>
    </form>
    <button v-on:click='dealData'>清洗</button>
    <hr/>
    <blog-footer></blog-footer>

  </div>
  
</template>

<style>
   .ready_area{
     text-align: left;
     margin-top:20px;
   }
   input{
     height: 25px;
     width: 200px;
     padding:0px 5px;
   }
    select{
      height: 30px;
      width: 200px;
    }
    .required>span{
      color: red;
    }
    .formArea{
      width: 100%;
    }
    .columnArea{
      float: left;
      min-height: 60px;
    }
    .columnArea>span{
      position: relative;
      float: left;
      line-height: 30px;
      width: 25%;
      text-align: right;
    }
    .columnArea>div{
      position: relative;
      float: left;
      width: 70%;
    }
    .columnArea>div>input{
      width: 100%;
    }
    .fd-w25{
      width: 25%;
    }
    .float_left{
      float: left;
    }
    .errorMsg{
      position: absolute;
      left:0px;
      top:35px;
      color: red;
    }
</style>


<script>
import blogHeader from '@/components/common/BlogHeader.vue'
import blogFooter from '@/components/common/BlogFooter.vue'

export default {
  name: 'BlogLogin',
  // blogHeader、blogFooter组件给申明到components里面然后在template里面使用
  components: { blogHeader, blogFooter },
  data () {
    return {
      responseResult: [],
      database:{
        dataBaseType:'postgresql',
        ip:null,
        port:null,
        dataBaseName:null,
        userName:null,
        password:null,
        sourceSechma:null,
        sourceTable:null,
        sourceColumn:null,
        purposeSechma:null,
        tempTable:null,
        oldColumn:null,
        newColumn:null,
      },
      dataBaseList:[
          {key: 'postgresql', value: "postgresql"},
          //{key: 'mysql', value: "mysql"}
      ]
    }
  },
  methods: {
    dealData () {

      this.$axios
        .post('/database', this.database)
        .then(successResponse => {
          this.responseResult = JSON.stringify(successResponse.data)
          alert(successResponse.data.message);
         
        })
        .catch(failResponse => {})
    }
  }
}
</script>



